@charset "UTF-8";
/* GLOBAL */
/* здесь используйте глобальные переменные */
/* здесь используйте глобальные селекторы img, body, html и так далее */
img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.wrapper {
  min-width: 280px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .wrapper {
    padding: 0 30px;
  }
}
@media (min-width: 1280px) {
  .wrapper {
    max-width: 1220px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* здесь подключите шрифты */
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/oswaldregular.woff2") format("woff2");
}
/* BLOCKS */
.section {
  position: relative;
}
.section__title {
  margin: 0;
  padding: 0;
  font-family: "Oswald", san-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: #000000;
}
@media (min-width: 768px) {
  .section__title {
    font-size: 60px;
    line-height: 60px;
  }
}

.button {
  display: block;
  width: 280px;
  padding: 11px 26px;
  box-sizing: border-box;
  font-family: "Oswald", san-serif;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .button {
    width: 244px;
    padding: 14px 26px;
    font-size: 20px;
    line-height: 20px;
  }
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}
.button--secondary {
  background-color: #f2f2f2;
  font-size: 16px;
  color: #444444;
}

.header {
  color: #ffffff;
}
@media (min-width: 768px) {
  .header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
  }
}
.header__container {
  position: relative;
}
@media (min-width: 768px) {
  .header__container {
    min-height: 78px;
    padding-top: 12px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__bar {
  padding-top: 12px;
  padding-bottom: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .header__bar {
    padding: 0;
  }
}
.header__logo {
  width: calc(50% + 50.5px);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .header__logo {
    width: auto;
  }
}
.header__toggler {
  width: 24px;
  height: 16px;
  display: block;
  background-color: transparent;
  border: none;
}
.header__toggler--closed {
  background: #000000;
  mask-image: url("../../icons/menu_button.svg");
}
.header__toggler--opened {
  position: relative;
}
.header__toggler--opened::before, .header__toggler--opened::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 25px;
  height: 1.5px;
  background-color: #000000;
}
.header__toggler--opened::before {
  transform: rotate(45deg);
}
.header__toggler--opened::after {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .header__toggler {
    display: none;
  }
}

.nav {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  z-index: 2;
}
.nav--closed {
  display: none;
}
.nav--opened {
  display: block;
}
.nav--nojs {
  display: block;
  position: relative;
}
@media (min-width: 768px) {
  .nav {
    width: auto;
    position: relative;
    top: auto;
    display: flex;
  }
}
@media (min-width: 768px) {
  .nav {
    background-color: transparent;
  }
}
.nav__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    gap: 34px;
    border-top: none;
  }
}
.nav__item {
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .nav__item {
    border-bottom: none;
  }
}
.nav__link {
  position: relative;
  display: block;
  padding: 22px 0 24px;
  position: relative;
  font-family: "Oswald", san-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
  text-align: center;
}
.nav__link[href]:hover {
  opacity: 0.6;
}
.nav__link[href]:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .nav__link {
    padding: 10px 0;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
@media (min-width: 1280px) {
  .nav__link {
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
    color: #ffffff;
  }
}
@media (min-width: 768px) {
  .nav__link--current::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1280px) {
  .nav__link--current::before {
    background-color: #ffffff;
  }
}
.nav__link--color-black {
  color: #111111;
}
.nav__link--color-black.nav__link--current::before {
  background-color: #68b738;
}
.nav__button {
  border: none;
  padding: 0;
}
@media (min-width: 768px) {
  .nav__button {
    display: none;
  }
}

.page {
  box-sizing: border-box;
}
.page::before, .page::after {
  box-sizing: inherit;
}
.page__body {
  margin: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}

.catalog-page {
  padding-top: 188px;
}
.catalog-page__title {
  margin: 0;
  padding: 0;
  margin-bottom: 84px;
  font-family: "Oswald", san-serif;
  font-size: 60px;
  line-height: 60px;
}

.additional-product__name {
  margin: 0;
  font-family: "Oswald", san-serif;
  font-size: 20px;
  line-height: 20px;
  color: #222222;
  text-transform: uppercase;
}
.additional-product__details {
  margin: 0;
  display: grid;
  grid-template-columns: subgrid;
  grid-column: 2/4;
}
.additional-product__weight {
  margin: 0;
}
.additional-product__cost {
  margin: 0;
}
.additional-product__button {
  padding: 10px 26px;
  font-size: 16px;
}

.additional-products {
  margin-bottom: 86px;
}
.additional-products__title {
  position: relative;
  display: inline-block;
  margin: 0;
  padding: 0;
  margin-bottom: 81px;
  font-family: "Oswald", san-serif;
  font-size: 36px;
  line-height: 36px;
}
.additional-products__title::before {
  content: "";
  position: absolute;
  top: 25px;
  right: calc(100% + 40px);
  width: 60%;
  height: 2px;
  background-color: #68b738;
  border-radius: 2px;
}
.additional-products__title::after {
  content: "";
  position: absolute;
  top: 25px;
  left: calc(100% + 40px);
  width: 100vw;
  height: 2px;
  background-color: #68b738;
  border-radius: 2px;
}
.additional-products__container {
  width: 100%;
  display: grid;
  grid-template-columns: auto 250px;
  gap: 80px;
}
.additional-products__list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  border-top: 1px solid #cdcdcd;
}
.additional-products__item {
  padding: 15px 0;
  display: grid;
  grid-template-columns: auto 150px 50px 244px;
  column-gap: 40px;
  align-items: center;
  border-bottom: 1px solid #cdcdcd;
}

.card {
  position: relative;
  display: grid;
  grid-template-areas: "image image" "title title" "details details" "button button";
  padding: 160px 35px 40px 40px;
  background-color: #f2f2f2;
}
.card--show-all {
  background-color: #ffffff;
  border: 2px solid #f2f2f2;
  grid-template-areas: ". ." "title title" "text text" "text text" "text text" "button button";
}
.card__title {
  margin: 0;
  grid-area: title;
  font-family: "Oswald", san-serif;
  font-size: 20px;
  line-height: 21px;
  text-transform: uppercase;
  text-align: center;
  color: #222222;
}
.card__text {
  margin: 0;
  grid-area: text;
  line-height: 18px;
  text-align: center;
  color: #444444;
}
.card__img {
  grid-area: image;
}
.card__details {
  margin-bottom: 33px;
  grid-area: details;
  gap: 16px;
  line-height: 16px;
  color: #444444;
}
.card__row {
  display: flex;
  justify-content: space-between;
  padding-bottom: 8px;
  margin-bottom: 6px;
  border-bottom: 1px solid #cdcdcd;
}
.card__description {
  margin: 0;
  text-align: right;
}
.card__button {
  grid-area: button;
  padding: 10px 35px;
  font-size: 16px;
  align-self: flex-end;
}

.contacts__container {
  padding-top: 26px;
