  font-family: "Oswald", san-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .example__range-text {
    position: relative;
    font-size: 20px;
    line-height: 30px;
  }
  .example__range-text::before {
    content: "";
    position: absolute;
    bottom: -5px;
    left: -0.5px;
    right: -0.5px;
    height: 5px;
    background-image: linear-gradient(to right, #000000 50%, transparent 50%);
    background-repeat: repeat-x;
    background-size: 4px 2px;
    opacity: 0.24;
  }
  .example__range-text:hover {
